.fill {
  height: 100vh; }

.logo {
  height: 4vmax;
  margin: 1vw;
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1); }
  .logo:hover {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    z-index: 100;
    cursor: pointer; }

.flex-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around; }

.flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.light {
